import React from 'react';
import { SubTitle } from 'components/atoms/Texts/Titles';
import {TextInput} from 'components/atoms/TextInputs';
import {RequestBookBtn} from 'components/atoms/Btns';
import axios from 'axios';
import Cookies from 'js-cookie';

const RequestBook = () => {
    const userNickName = window.localStorage.getItem('user');

    const [bookTitle, setBookTitle] = React.useState<string>('');
    const [bookAuthor, setBookAuthor] = React.useState<string>('');
    const [interestedSection, setInterestedSection] = React.useState<string>('');
    const [etcInfo, setEtcInfo] = React.useState<string>('');

    const requestBook = () => {
        const csrftoken = Cookies.get('csrftoken');
        if(userNickName){
            if (bookTitle && bookAuthor) {
                axios.post(`${process.env.REACT_APP_BASE_URL}/book/request/`, {
                    headers: {
                        "Access-Control-Allow-Origin": '*',
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'X-CSRFToken': csrftoken
                    },
                    data: {
                        g_school_nickname: userNickName,
                        book_title: bookTitle,
                        author: bookAuthor,
                        interest_parts: interestedSection,
                        others: etcInfo
                    }
                }).then((res) => {
                    window.alert(" .");
                }).catch((e) => console.error(e))
            } else {
                window.alert("  .");
            }
        } else {
            window.alert("    .");
        }
    };

    React.useEffect(() => {
        if (bookTitle && bookAuthor) {
            document.getElementById("request-btn")!.style.backgroundColor = 'black';
            document.getElementById("request-btn")!.style.color = 'white';
        } else {
            document.getElementById("request-btn")!.style.backgroundColor = '#DFDFDF';
            document.getElementById("request-btn")!.style.color = '#C2C3CB';
        }
    }, [bookTitle, bookAuthor])

    return (
        <div style={{
            display:'flex',
            height: '80%',
            width: '50%',
            marginTop:'10%',
            flexDirection:'column',
            justifyContent: 'flex-start',
            alignItems: 'center'
        }}>
            <div style={{
                display: 'flex',
                flexDirection:'column',
                justifyContent: 'center',
                alignItems: 'center'
            }}>
                <h1></h1>
                <h3>{`   ! `}</h3>
            </div>

            <SubTitle fontSize="15px" margin='10px 0' text="*" />
            <TextInput value={bookTitle} placeholder=' ' onChange={(e:any) => setBookTitle(e.target.value)} />

            <SubTitle fontSize="15px" margin='20px 0px 5px 0px' text="*" />
            <TextInput value={bookAuthor} placeholder=' ' onChange={(e:any) => setBookAuthor(e.target.value)} />
            
            <SubTitle fontSize="15px" margin='20px 0px 5px 0px' text=" " />
            <TextInput value={interestedSection} placeholder='  ' onChange={(e:any) => setInterestedSection(e.target.value)} />

            <SubTitle fontSize="15px" margin='20px 0px 5px 0px' text="" />
            <TextInput value={etcInfo} placeholder='  ' onChange={(e:any) => setEtcInfo(e.target.value)} />

            <RequestBookBtn id="request-btn" text="" onClick={requestBook} margin="20px 0 0 0" />

        </div>
    )
};

export default RequestBook;